// 이벤트 메인
.event_main_page {

	.pagination {
		margin-top: 30px;
	}
}

/* 이벤트 메인 비쥬얼 스와이퍼 영역 */
.event_main_visual_wrap {

	.event_main_visual {
		.swiper-container {
			border-radius: 30px 0 30px 30px;
		}
	}

	.swiper-slide {
		overflow: hidden;
	}
}

/* 이벤트 메인 추천 영역 */
.event_recommend_list_wrap {
	@include mixins.justify_width;
	margin-top: 30px;
	padding: 14px 15px 4px 25px;
	background: #f8f8fc;
	border-radius: 10px;

	.recommend_list_title {
		flex: 1;
		color: variables.$textColorPurple;
		margin-right: 70px;
		padding-left: 24px;
		background: {
			position: left top 5px;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_like_active', 'png', 20px, 20px);
		font-size: variables.$fontSizeXxs;
		line-height: 36px;
		letter-spacing: variables.$letterSpacing;
		font-weight: variables.$fontWeightBold;
	}
	.event_recommend_list {
		flex: 0 0 1042px;
		.event_recommend_item {
			display: inline-block;
			margin: 0 10px 10px 0;

			a {
				display: inline-block;
				height: 36px;
				padding: 0 16px;
				border-radius: 18px;
				background: #fff;
				line-height: 36px;

				.text {
					@include mixins.font_size_xxs;
				}
			}
		}
	}
}

.event_main_menu {
	margin-top: 50px;

	[class^=ico_event] {
		display: block;
		margin: 0 auto 10px;
	}

	ul {
		@include mixins.justify_width(center, center);
		flex-wrap: wrap;

		li {
			margin-left: 61px;
			text-align: center;

			&:first-child {
				margin-left: 0;
			}
		}
	}
}
.event_search_box {
	margin-top: 70px;
	.form_search_box {
		margin-left: auto;
	}
}
.event_main_tag_wrapper {
	.swiper-slide {
		width: auto;
	}
}

.event_main_tag_wrapper {
	margin: 30px 0;
}

.event_main_list_wrap {

	.list_result_wrap {
		margin-bottom: 9px;
	}

	.event_list {
		padding-bottom: 40px;
	}
	.no_data {
		border-top: none;
	}
}
[class^=ico_event] {
	display: inline-block;
	width: 94px;
	height: 94px;
	border-radius: 50%;
	background: {
		position: center center;
		repeat: no-repeat;
		color: variables.$colorGray06;
	}
}
.ico_event_main01 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_01', 'png', 46px, 46px);
}
.ico_event_main02 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_02', 'png', 46px, 46px);
}
.ico_event_main03 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_03', 'png', 46px, 46px);
}
.ico_event_main04 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_04', 'png', 46px, 46px);
}
.ico_event_main05 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_05', 'png', 46px, 46px);
}
.ico_event_main06 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_06', 'png', 46px, 46px);
}
.ico_event_main07 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_07', 'png', 46px, 46px);
}
.ico_event_main08 {
	@include mixins.ratio_images('../images/ico_event_mainMenu_08', 'png', 46px, 46px);
}

// 컨텐츠 와 footer 여백이 없을때 container_wrapper 추가
.footer_space_none {
	& ~ .footer_wrapper {
		margin-top: 0;
	}
}

// 이벤트 상품 sps
.tab_wrap.type_box {
	.tab_list_wrap {
		height: 50px;
	}
}
.tab_wrap.type_tag {
	.tab_list_wrap {
		height: 34px;

		.sps-blw {
			border-bottom: 1px solid variables.$colorGray04;

			.sps_inner {
				padding: 20px 0;
			}
			.tabs {
				.tab_item {
					margin-bottom: 0;
				}
			}
		}
		.sps-blw[data-add-offset="52"] {
			top: 131px;
		}
	}
}
.view_detail_body {
	
	.tab_content {
		.title_wrap {
			margin-top: 40px;
		}
	}
	.switch_prod_wrap.view_type_list {
		.prod_list {
			.prod_item {
				.prod_area {
					max-width: 930px;
				}
			}
		}
	}

}

// 이벤트 행사도서
.event_prod_book {
	& ~ .event_prod_book {
		margin-top: 60px;
	}

	.title_wrap {
		margin-top: 40px;
	}
	.btn_wrap {
		margin-top: 40px;
		text-align: center;
	}
}

// 이벤트 상세 썸네일 배너
.event_sticky_banner {
	text-align: center;

	.close {
		width: 25px;
		height: 25px;
		margin-top: 6px;
		cursor: pointer;
		@include mixins.ratio_images('../images/btn_event_close', 'png', 25px, 25px);

	}
}

// 추천하는 이벤트
.recommend_event_wrapper {
	position: relative;

	.recommend_event_wrap {
		background-color: #f8f8fc;
		border: {
			top: 1px solid variables.$colorGray04;
			bottom: 1px solid variables.$kyoboPurple;
		}
	}
}
.recommend_event_wrap {
	display: none;

	&.sps-blw {
		display: block;
	}
}
.recommend_event {
	position: relative;
	padding: 12px 0 12px 204px;

	.title {
		position: absolute;
		left: 0;
		top: 50%;
		@include mixins.transform(translateY(-50%));
		@include mixins.font_size_def;
		font-weight: variables.$fontWeightBold;
		color: variables.$textColorPurple;

		.ico_like {
			display: inline-block;
			width: 20px;
			height: 20px;
			margin-right: 6px;
			vertical-align: middle;
			@include mixins.transform(translateY(-2px));
			@include mixins.ratio_images('../images/ico_like_active', 'png', 20px, 20px);
			background: {
				repeat: no-repeat;
				position: center center;
			}
		}
	}
}
.recommend_list {
	@include mixins.justify_width;
	flex-wrap: wrap;

	li {
		margin-left: 45px;

		&:nth-child(5n + 1) {
			margin-left: 0;
		}
	}

	a {
		display: block;
		width: 160px;
		height: 36px;
		line-height: 36px;
		text-align: center;
		border-radius: 19px;
		@include mixins.transition(font-weight .23s, background-color .23s);

		&:hover,
		&:focus {
			color: variables.$textColorPurple;
			font-weight: variables.$fontWeightMedium;
			background-color: rgba(variables.$kyoboPurple, .1);
		}
	}
}

// 이벤트 특별 사은품
.event_section {
	margin-top: 60px;

	&.first {
		margin-top: 0;
	}

	.inner {
		width: 680px;
		margin: auto;
	}

	.event_head {
		margin-bottom: 30px;

		.event_title {
			margin-bottom: 10px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.event_title {
		margin-bottom: 10px;
		@include mixins.font_size_md;
		font-weight: variables.$fontWeightBold;
		text-align: center;
	}

	.info_text_box {
		margin-top: 20px;
	}

	~ .event_section.has_line {
		padding-top: 60px;
		border-top: 1px solid variables.$colorGray04;
	}

	.round_gray_box {
		margin-top: 20px;

		.info_desc {
			@include mixins.font_size_def;
			font-weight: variables.$fontWeightMedium;
		}

		.inner {
			@include mixins.justify_width(center, between);
		}

		&.hash_tag {
			.inner {
				@include mixins.justify_width(center, center, column);
			}

			.hash_tag_list {
				margin-top: 7px;
			}

			.btn_wrap {
				margin-top: 15px;
			}
		}
	}

	.round_gray_box.person_info_wrapper {

		&:first-child {
			margin-top: 0;
		}

		.inner {
			display: block;
			width: 700px;
		}

		.person_info_area {
			padding: 0;
			border: 0;
			border-radius: 0;

			&.type_text {
				.profile_info {
					margin: 0;
					padding: 0;
					text-align: center;
				}
			}
		}
		.person_desc {
			@include mixins.font_size_xs;
			-webkit-line-clamp: inherit;
			max-height: none;
		}

		.btn_wrap {
			margin-top: 24px;
			text-align: center;
		}
	}

	.event_url {
		.input_btn_box {
			width: 302px;
		}
	}

	.prod_list {
		padding-bottom: 40px;
		border-bottom: 1px solid variables.$colorGray03;
	}

	.btn_line_primary.current {
		.ico_heart_primary {
			@include mixins.ratio_images('../images/ico_heart_h18_active', 'png', 18px, 18px);

		}
	}
}
// 이벤트 정보
.event_info_wrap {
	@include mixins.justify_width(start, between);

	.imgs {
		overflow: hidden;
		width: 670px;
		height: 362px;
		border-radius: 20px;

		img {
			position: relative;
			left: 50%;
			@include mixins.transform(translateX(-50%));
		}

		+ .info {
			width: 506px;
			height: 362px;
			padding: 36px 30px 10px;
			border: 1px solid variables.$colorGray04;
			border-radius: 20px;
		}
	}

	.info {
		box-sizing: border-box;

		li {
			@include mixins.justify_width;
			margin-top: 18px;

			&:first-child {
				margin-top: 0;
			}

			.name {
				width: 140px;
				font-weight: variables.$fontWeightMedium;
			}

			.desc {
				flex: 1;
				color: variables.$textColorLightGray;
			}
		}

		.btn_wrap {
			margin-top: 30px;

			.btn_text {
				display: block;
				height: 50px;
				border-radius: 6px;
				@include mixins.font_size_def;
				line-height: 50px;
				font-weight: variables.$fontWeightBold;
				text-align: center;
				background-color: variables.$colorGray06;
			}
		}
	}
}

// 이벤트 sns 공유 이미지
.event_imgs_down {
	overflow: hidden;
	padding: 70px 0;
	border-radius: 20px;
	background: {
		repeat: no-repeat;
		position: center;
	}

	@include mixins.ratio_images('../images/bg_event_share_img', 'png', 1200px, 400px);

	.inner {
		@include mixins.justify_width(center, between);
	}

	.left_area {
		width: 310px;
	}

	.title {
		@include mixins.font_size_md;
		font-weight: variables.$fontWeightBold;
	}

	.text {
		margin-top: 15px;
		color: rgba(variables.$textColorBlack, .7);
	}

	.btn_down {
		width: 58px;
		height: 58px;
		border-radius: 50%;
		background-color: variables.$kyoboPurple;

		.ico_down {
			display: inline-block;
			width: 10px;
			height: 17px;
			@include mixins.ratio_images('../images/ico_down_10x17', 'png', 10px, 17px);
			background: {
				repeat: no-repeat;
				position: center center;
			}
		}
	}

	.btn_wrap {
		margin-top: 40px;
		text-align: left;
	}

	.img_crop {
		overflow: hidden;
		width: 260px;
		height: 260px;
		border-radius: 20px;

		img {
			position: relative;
			left: 50%;
			top: 50%;
			@include mixins.transform(translate(-50%, -50%));
		}
	}
}

// 이벤트 신청
.event_application {
	.round_gray_box {
		margin: 20px 0;

		&:first-child {
			margin-top: 0;
		}
	}

	.title_wrap.title_size_def {
		margin-top: 30px;

		&:first-child {
			margin-top: 0;
		}
	}

	.form_cont {
		.bul_item_asterisk {
			margin-top: 8px;
		}
	}

	.btn_wrap {
		margin-top: 8px;

		&.align_left {
			text-align: left;
		}
	}
}

// 이벤트 기본 swiper
.gallery_single {
	position: relative;
	width: 850px;
	margin: auto;

	.code {
		margin-bottom: 30px;
		text-align: center;
		@include mixins.font_size_def;
		color: variables.$textColorGray;
	}

	.imgs {
		overflow: hidden;
		position: relative;
		border-radius: 20px;
	}

	.desc {
		margin-top: 24px;
		@include mixins.font_size_xxs;
		text-align: center;
		color: variables.$textColorGray;
	}

	.btn_detail {
		position: absolute;
		right: 30px;
		top: 30px;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		@include mixins.ratio_images('../images/btn_book_cast_search', 'png', 18px, 18px);
		background: {
			repeat: no-repeat;
			position: center center;
			color: #fff;
		};
	}

	.swiper-button-prev {
		left: -22px;
	}

	.swiper-button-next {
		right: -22px;
	}

	.swiper-pagination {
		bottom: 30px;
	}

	&.has_desc {
		.swiper-pagination {
			bottom: 70px;
		}
	}
}

.event_gallery_swiper {
	width: 850px;
	margin: 0 auto;
}
.gallery_big {
	.code {
		margin-bottom: 30px;
		text-align: center;
		@include mixins.font_size_def;
		color: variables.$textColorGray;
	}

	.imgs {
		overflow: hidden;
		position: relative;
		border-radius: 20px;
	}

	.btn_detail {
		position: absolute;
		right: 30px;
		top: 30px;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		@include mixins.ratio_images('../images/btn_book_cast_search', 'png', 18px, 18px);
		background: {
			repeat: no-repeat;
			position: center center;
			color: #fff;
		};
	}

	.sold_out {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		@include mixins.justify_width(center, center, column);
		background-color: rgba(variables.$textColorBlack, .5);
		color: #fff;

		p {
			margin-top: 14px;
			@include mixins.font_size_md;
			font-weight: variables.$fontWeightBold;
		}
	}
}
.gallery_thumb {
	position: relative;
	margin-top: 16px;

	.swiper-slide {
		overflow: hidden;
		width: 140px;
		height: 108px;
		border-radius: 10px;

		img {
			max-width: 100%;
		}

		&.swiper-slide-thumb-active {
			&:before {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				border: 1px solid variables.$textColorBlack;
				border-radius: 10px;
			}
		}
	}

	.swiper-button-prev {
		left: -22px;
	}

	.swiper-button-next {
		right: -22px;
	}
}
.sns_share_url {
	@include mixins.justify_width(center, center, column);
	padding: 30px;
	border: 1px solid variables.$colorGray04;

	.desc {
		@include mixins.font_size_def;
		font-weight: variables.$fontWeightMedium;
	}

	.sns_share_list {
		margin-top: 20px;
	}
}
.sns_share_list {
	@include mixins.justify_width(center, center);

	li {
		margin: 0 8px;
		font-size: 0;

		button {
			cursor: pointer;
		}
	}
}
.hash_tag_list {
	@include mixins.justify_width(center, center);
	flex-wrap: wrap;

	li {
		margin: 5px;
		@include mixins.font_size_xxs;
		font-weight: variables.$fontWeightMedium;
		color: variables.$kyoboPurple;
	}
}

.quiz_wrapper {
	.sub_desc {
		.ico_quiz_bubble {
			@include mixins.transform(translateY(-3px));
		}
	}

	& + .bul_list {
		margin-top: 16px;
	}
