.champion {
	position: relative;
	height: 1080px;
	margin-top: 60px;

	.inner {
		position: relative;
		z-index: 2;
		box-sizing: border-box;
		height: 100%;
		padding: 105px 375px 30px;
		text-align: center;
		@include mixins.ratio_images('../images/bg_champion_line', 'png', 650px, 784px);
		background: {
			repeat: no-repeat;
			position: center bottom -1px;
		}
	}

	.head {
		margin-bottom: 60px;

		.desc {
			margin-bottom: 16px;
			@include mixins.font_size_xl;
			color: #fff;
		}
	}

	.book {
		@include mixins.justify_width(center, center, column);

		.img {
			overflow: hidden;
			width: 242px;
			height: 362px;
			margin-bottom: 32px;

			img {
				max-width: 100%;
			}
		}

		.book_name {
			margin-bottom: 13px;
		}
		.book_name,
		.book_name a,
		.book_desc {
			@include mixins.font_size_sm;
			color: #fff;
		}

		.custom_scroll_wrap {
			height: 70px;
		}

		.btn_wrap {
			margin-top: 47px;
			@include mixins.justify_width(center, center);
			
			.btn_circle {
				@include mixins.justify_width(center, center);
				width: 42px;
				height: 42px;
				border: 1px solid variables.$colorGray02;
				border-radius: 50%;
				background-color: rgba(0, 0, 0, .15);

				&.active {
					.ico_like {
						@include mixins.ratio_images('../images/ico_heart_h18_active', 'png', 18px, 18px);
					}
				}

				.ico_like {
					width: 18px;
					height: 18px;
					@include mixins.ratio_images('../images/ico_heart_18x18', 'png', 18px, 18px);
				}
				.ico_cart {
					width: 16px;
					height: 15px;
					@include mixins.ratio_images('../images/ico_cart_16x15', 'png', 16px, 15px);
				}
			}
		}
	}

	.btn_wrap {
		margin-top: 70px;

		.btn_xl {
			min-width: auto;
			margin: 0 4px;
			cursor: pointer;
			font-weight: variables.$fontWeightMedium;

			&:first-child {
				margin-left: 0;
			}
			&:last-child {
				margin-right: 0;
			}

			&.line {
				color: #fff;
				border: 1px solid #fff;
			}
			&.bg {
				background-color: #fff;
			}
			.ico_reset {
				display: inline-block;
				width: 13px;
				height: 13px;
				margin-right: 8px;
				vertical-align: middle;
				@include mixins.ratio_images('../images/ico_reset_13x13', 'png', 13px, 13px);
			}
			.ico_share {
				display: inline-block;
				width: 14px;
				height: 14px;
				margin-right: 8px;
				vertical-align: middle;
				@include mixins.ratio_images('../images/ico_share_14x14', 'png', 14px, 14px);
			}
		}
	}
}

