		padding: 16px 30px 16px 16px;
		border: 1px solid variables.$colorGray04;
		border-radius: 10px;

		&:first-child {
			margin-top: 0;
			border-color: variables.$kyoboPurple;

			.name {
				width: 44px;
				height: 27px;
				text-align: center;
				@include mixins.font_size_min;
				color: variables.$textColorPurple;
				@include mixins.ratio_images('../images/ico_champion_44x25', 'png', 44px, 25px);
				background: {
					repeat: no-repeat;
					position: center bottom;
				}

				.fw_bold {
					font-size: 17px;
				}
			}

			.progress {
				.bar {
					background-color: variables.$kyoboPurple;
				}
			}
		}

		&.active {
			border-color: variables.$colorGray06;
			background-color: variables.$colorGray06;
		}
	}
	.img {
		overflow: hidden;
		width: 64px;
		height: 93px;
		margin-right: 20px;
	}
	.info {
		position: relative;
		flex: 1;
		padding-bottom: 25px;

		.name {
			display: inline-block;
			@include mixins.font_size_def;
			font-weight: variables.$fontWeightMedium;
		}
		.progress {
			margin: 10px 0 8px;
			background-color: #eaeaea;
		}
		.progress_info {
			position: relative;
			@include mixins.font_size_xxs;

			.percent {
				font-weight: variables.$fontWeightBold;
			}
			.val {
				color: variables.$textColorLightGray;
			}
		}
		.my_select {
			position: absolute;
			right: 0;
			bottom: 0;
			@include mixins.font_size_min;
			padding-left: 16px;
			font-weight: variables.$fontWeightBold;
			color: variables.$kyoboPurple;

			&:before {
				content: "";
				position: absolute;
				left: 0;
				top: 50%;
				width: 10px;
				height: 7px;
				@include mixins.transform(translateY(-50%));
				@include mixins.ratio_images('../images/ico_my_select_10x7', 'png', 10px, 7px);
			}
		}
	}
}


